@mixin arrow-seconds-color {
  background: radial-gradient(circle, rgba(96,112,240,1) 0%,
  rgba(162,133,218,1) 100%);
}

@mixin background-color {
  background: radial-gradient(circle, rgba(251,63,132,1) 0%,
  rgba(242,195,18,0.86) 100%);
}

@mixin arrow-gradient {
  background: radial-gradient(circle, rgba(60,141,190,1) 0%,
  rgba(98,190,85,1) 100%);
}

@mixin watch-size {
  height: $watch-height;
  width: $watch-weight;
}

@mixin size-middle {
  height: 7px;
  width: 7px;
}
